<template>
	<div class="info-form">
		<Form ref="formData" :model="formValidate" :label-width="80" :rules="ruleValidate">
			<FormItem label="手机号" prop="phone">
				<i-input v-model="formValidate.phone" clearable size="large" placeholder="请输入手机号"></i-input>
			</FormItem>
			<FormItem label="验证码" prop="checkNum">
				<i-input v-model="formValidate.checkNum" size="large" placeholder="请输入验证码">
					<Button v-if="checkNumSent" slot="append" disabled>重新发送 ({{time}}s)</Button>
					<Button v-else slot="append" @click="getCheckNum">获取验证码</Button>
				</i-input>
			</FormItem>
			<Button type="error" size="large" long @click="handleSubmit">验证手机号</Button>
		</Form>
	</div>
</template>

<script>
	import {mapMutations} from 'vuex';

	export default {
		name: 'CheckPhone',

		data() {
			return {
				formValidate: {
					phone: '',
					checkNum: '',
				},

				ruleValidate: {
					phone: [
						{required: true, message: '手机号不能为空', trigger: 'blur'},
						{type: 'string', pattern: /^1[3|4|5|7|8|9][0-9]{9}$/, message: '手机号格式出错', trigger: 'blur'}
					],
					checkNum: [
						{required: true, message: '必须填写验证码', trigger: 'blur'},
						{type: 'string', min: 6, max: 6, message: '验证码长度错误', trigger: 'blur'}
					]
				},

				checkNumSent: false,
				time: 60,
			};
		},

		mounted() {
			this.SET_SIGN_UP_STEP(0);
		},

		methods: {
			...mapMutations(['SET_SIGN_UP_STEP']),

			countDown() {
				if (this.time > 0) {
					setTimeout(() => {
						this.time--;
						this.countDown();
					}, 1000);
				} else {
					this.time = 60;
					this.checkNumSent = false;
				}
			},

			async getCheckNum() {
				if (this.formValidate.phone.length === 11) {
					let res = await this.$request.sendSms(this.formValidate.phone);
					if (res) {
						this.$Message.success({
							content: '验证码已发送',
							duration: 3,
							closable: true
						});
						this.checkNumSent = true;
						this.countDown();
					} else {
						this.$Message.error({
							content: '验证码发送失败',
							duration: 3,
							closable: true
						});
					}
				} else {
					this.$Message.error({
						content: '请输入正确的手机号',
						duration: 3,
						closable: true
					});
				}
			},

			handleSubmit() {
				this.$refs['formData'].validate(async (valid) => {
					if (valid) {
						let res = await this.$request.checkCode(this.formValidate.phone, this.formValidate.checkNum);
						if (res) {
							this.$router.push({
								path: '/SignUp/inputInfo',
								query: {
									phone: this.formValidate.phone
								}
							});
							this.SET_SIGN_UP_STEP(1);
						} else {
							this.$Message.error({
								content: '验证码错误',
								duration: 3,
								closable: true
							});
						}
					} else {
						this.$Message.error({
							content: '请填写正确的信息',
							duration: 3,
							closable: true
						});
					}
				});
			}
		},
	};
</script>

<style scoped>
	.info-form {
		width: 90% !important;
	}
</style>



